//   
import React, {useEffect} from 'react';
import {useDispatch, useSelector} from "react-redux";

//MUI
import {Button, ButtonGroup} from "@mui/material";

//
import {changeTicketStatus} from "../../../store/tickets";
import {getAllProfiles} from "../../../store/profile";

/** Include Styles */
import './ticket.module.css'

export const Ticket = ({el, isAdmin = false, fio = null, uid}) => {
    const dispatch = useDispatch();
    // const {profiles, status} = useSelector((state) => state.profile);

    // useEffect(() => { //      
    //     if (!isAdmin) {
    //         dispatch(getAllProfiles())
    //     }
    // }, [dispatch])

    // /**
    //  *   Uid   
    //  * @param ticketAuthorFirstName {String}  
    //  * @param ticketAuthorLastName {String}  
    //  * @returns {String} user uid/ uid 
    //  */
    // const getUserUid = ({ticketAuthorFirstName, ticketAuthorLastName}) => {
    //     //             
    //     let profile = profiles.filter(elem => elem.firstName === ticketAuthorFirstName && elem.lastName === ticketAuthorLastName)
    //
    //     return profile[0].uid
    // }

    /**
     *    
     * @param ticket {Object} 
     * @param ticketStatus {String}  
     * @param userCompleted {Boolean}   
     * @returns {Promise<void>}
     */
    const changeTicketHandler = (ticket, ticketStatus, userCompleted = false) => {
        el.ticketStatus = ticketStatus //  (  )   
        el.userCompleted = userCompleted //      

        if (isAdmin) {
            el.ticketExecutor = fio //     
        }

        // debugger

        // const profileUid = getUserUid(ticket) //  uid   
        console.log('', fio)
        // console.log("PROFILES", profileUid)
        /**
         *  dispatch     ,   
         * Ticket 
         * isAdmin   
         */
        // dispatch(changeTicketStatus(ticket, fio, profileUid))
        dispatch(changeTicketStatus(ticket, uid, isAdmin))

        console.log('')
    }

    return (
        <div style={{
            border: "solid 1px grey",
            borderRadius: "5px",
            backgroundColor: "#05050008",
            marginBottom: "5px",
            padding: "5px",
            textAlign: "left",
            color: el.userCompleted ? 'rgba(0,0,0,0.17)' : ''
        }}>
            <div><span>: </span><span>{el.ticketDate}</span></div>
            <div>
                <span>: </span><span>{el.ticketAuthorLastName} {el.ticketAuthorFirstName}</span>
            </div>
            <div>
                <span>: </span><span>{el.ticketImportance === 'low' ? " " : el.ticketImportance === 'normal' ? "  " : ""}</span>
            </div>
            <div>
                <span>: </span><span>{el.ticketStatus}</span>

                {/*<span>: </span><span>{el.ticketStatus === 'sent' ? "" : el.ticketStatus === 'processed' ? "" : ""}</span>*/}
                <br/>
                <span>:</span><span>{el.ticketExecutor ?? "  "}</span>
            </div>
            {isAdmin ? <div>
                <span>  : </span><span>{el.userCompleted === true ? '' : ' '}</span>
            </div> : <></>
            }
            <div><span> :</span></div>
            <div><span><span>{el.ticketText}</span></span></div>
            {/*           
                , 
            */}
            {
                isAdmin ?
                    <ButtonGroup
                        variant="outlined"
                        size='ticketButtonSize' //    custom-theme-provider
                        aria-label="outlined button group"
                        sx={{
                            display: 'flex',
                            justifyContent: 'center'
                        }}
                    >
                        <Button
                            disabled={el.ticketStatus === ' '}
                            onClick={() => changeTicketHandler(el, ' ')}> </Button>
                        <Button
                            disabled={el.userCompleted}
                            onClick={() => changeTicketHandler(el, '')}
                        ></Button>
                        <Button
                            disabled={el.userCompleted}
                            onClick={() => changeTicketHandler(el, ' ', true)}
                        > </Button>
                    </ButtonGroup>
                    :
                    //      
                    // , ,  true   
                    !el.userCompleted
                        ?
                        <Button
                            variant="outlined"
                            onClick={() => changeTicketHandler(el, ' ', true)}
                        >
                             
                        </Button>
                        :
                        //     
                        <Button variant="outlined"
                                onClick={() => changeTicketHandler(el, ' ', false)}
                        >  </Button>

            }
        </div>
    );
};